.gallery_grid{
      gap: .2rem;
}

.gallery_row{
      height: 27.5rem;
      position: relative;
      overflow: hidden;
}

.gallery_row__img{
      width: 100%;
      height: 100%;
      overflow: hidden;
}

.gallery_row__img:before{
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background: rgba(96, 96, 96, 0.2);
      -webkit-backdrop-filter: blur(5px);
      backdrop-filter: blur(5px);
      opacity: 0;
      transition: .2s ease-in-out;
}

.gallery_row:hover .gallery_row__img::before{
      opacity: 1;
}

.btn_gallery{
      position: absolute;
      width: 4.4rem;
      height: 4.4rem;
      z-index: 2;
      top: 1rem;
      right: 1rem;
      background: rgba(96, 96, 96, 0.2);
      border-radius: 50%;
      color: var(--light);
}

.btn_gallery *{
      font-size: 2.4rem;
}