.online_content{
      align-items: center;
      gap: 1rem;
}

.online_avatar{
      position: relative;
      width: 4.6rem;
      height: 4.6rem;
      border-radius: 50%;
      border: solid .3rem var(--light);
}

.online_avatar__img{
      position: absolute;
      width: 3.6rem;
      height: 3.6rem;
      border-radius: 50%;
      overflow: hidden;
}

.online_dot{
      position: absolute;
      width: 1.4rem;
      height: 1.4rem;
      border: solid .3rem var(--light);
      border-radius: 50%;
      bottom: 0;
      right: -.5rem;
}

.online_dot.green{
      background: var(--green);
}

.online_dot.red{
      background: var(--red);
}

.online_content span{
      padding: .3rem 1rem;
      border-radius: 999px;
      border: solid .3rem var(--light);
      font-size: 1.4rem;
}