.aside_content{
      width: 30rem;
      height: 100vh;
      position: fixed;
      top: 0;
      left: 0;
}

.aside_banner{
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      overflow: hidden;
}

.aside_banner__img{
      width: 100%;
      height: 100%;
      align-items: center;
      justify-content: center;
}

.aside_banner__img:before{
      width: 100%;
      height: 100%;
      background: rgba(255, 255, 255, .3);
      -webkit-backdrop-filter: blur(5px);
      backdrop-filter: blur(5px);
}

.aside_banner__img img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.aside_content__footer{
      width: 100%;
      position: absolute;
      padding: 2.5rem;
      bottom: 5rem;
}

.aside_social{
      margin-top: 2rem;
}

.social_menu{
      width: 100%;
      align-items: center;
      gap: 1rem;
}