.friends_content{
      width: 38rem;
}

.friend_box{
      width: 10rem;
      padding: .5rem;
}

.friend_item__box{
      flex-direction: column;
      align-items: center;
}

.friend_item__avatar{
      position: relative;
      width: 9rem;
      height: 9rem;
      border: solid .2rem, var(--border);
      border-radius: 50%;
      overflow: hidden;
}

.friend_item__avatar_img{
      width: 8rem;
      height: 8rem;
      border-radius: 50%;
      overflow: hidden;
}

.friend_item__box a{
      width: 8rem;
      height: 8rem;
      display: block;
      position: absolute;
      top: .25;
      left: .25rem;
      border-radius: 50%;
      background: #96abd8;
      opacity: 0;
      color: transparent;
      transition: .3s ease-in-out;
}

.friend_item__box:hover a{
      opacity: .7;
}

.friend_item__box > span{
      width: 100%;
      text-align: center;
      margin-top: .5rem;
      font-weight: 500;
      font-size: 1.4rem;
      color: var(--text-200);
}


button.slick-arrow{
      width: 4rem;
      height: 4rem;
      border-radius: 50%;
      z-index: 2;
      top: 5rem;
}

button.slick-arrow.slick-next{
      right: -3.2rem;
}

button.slick-arrow.slick-prev{
      left: -3.2rem;
}

button.slick-arrow.slick-next:before,
button.slick-arrow.slick-prev:before{
      width: 1.4rem;
      height: 1.4rem;
      color: transparent;
      transform: rotate(45deg);
      top: 1.2rem;
      left: .8rem;
}

button.slick-arrow.slick-next:before{
      border-top: solid .4rem var(--primary);
      border-right: solid .4rem var(--primary);
}


button.slick-arrow.slick-prev:before{
      border-bottom: solid .4rem var(--primary);
      border-left: solid .4rem var(--primary);
      left: 1.4rem;
}