.content{
      width: calc(100% - 30rem);
      height: auto;
      position: relative;
      transform: translateX(30rem);
      padding-left: 9.5rem;
}

.header_content{
      width: 77.5rem;
      height: auto;
      padding-top: 7rem;
      justify-content: flex-end;
}

.header_user__title{
      align-items: center;
      gap: 8rem;
}

.user_title__top h1{
      font-size: 2.8rem;
      font-weight: 600;
}

.user_title__top_id{
      justify-content: flex-end;
      align-items: center;
      gap: .5rem;
}

.user_title__top_id *{
      color: var(--primary);
      font-size: 2.6rem;
}

.user_title__top_id span{
      font-size: 2rem;
      color: var(--text-200);
}

.user_title__dots{
      width: .8rem;
      height: .8rem;
      background: var(--text-100);
      border-radius: 50%;
      box-shadow: -1.6rem 0 var(--text-100), -3.2rem 0 var(--text-100); 
}

.user_data{
      width: 77.5rem;
      margin-top: 2rem;
      align-items: center;
      gap: 12rem;
}

.user_avatar{
      width: 16.5rem;
      height: 16.5rem;
      border-radius: 50%;
      background: var(--v-gradient);
}

.user_avatar__img{
      width: 14rem;
      height: 14rem;
      border-radius: 50%;
      overflow: hidden;
      box-shadow: 0 0 0 .7rem var(--bg-300);
}

.user_amout{
      align-items: center;
      gap: 5rem;
}

.user_col__grid{
      flex-direction: column;
      align-items: center;
}

.user_col__grid strong{
      font-size: 4rem;
      font-weight: 500;
}

.user_col__grid span{
      font-weight: 400;
      color: var(--text-200);
}

.content_main{
      width: 48.2rem;
}

.user_data > span{
      font-family: var(--ft-6);
      font-size: 2.8rem;
}

.user_data > p{
      font-style: italic;
      color: var(--text-200);
}

.relation_type{
      margin-top: 1rem;
}

.relation_type p{
      font-size: 1.8rem;
}

.relation_type p a{
      background: var(--primary);
      padding: 0 1rem;
      height: 2rem;
      line-height: 1.8rem;
      border-radius: 999px;
      color: var(--text-100);
      font-size: 1.4rem;
}

.user_data__location{
      margin-top: 2rem;
      align-items: center;
      gap: .5rem;
}

.user_data__location *{
      font-size: 2.4rem;
      color: var(--primary);
}

.user_data__location span{
      color: var(--text-100);
}

.user_description{
      margin-top: 1rem;
      width: 48.2rem;
}

.user_description p{
      font-style: italic;
      white-space: pre-line;
}

.user_navigator{
      margin-top: 4rem;
      align-items: center;
      gap: 1rem;
}

.user_navigator a{
      display: flex;
      align-items: center;
      justify-content: center;
      width: 14rem;
      height: 4.4rem;
      border-radius: .4rem;
      color: var(--text-100);
      border: solid .1rem var(--border);
      transition: .2s ease-in-out;
      font-weight: 500;
}

.user_navigator a:hover{
      border: solid .1rem transparent;
      background: var(--bg-100);
      color: var(--text-300);
}

.content_pages{
      margin-top: 4rem;
}

.friends_group{
      width: 100%;
      align-items: center;
      justify-content: flex-start;
      margin-left: 3.5rem;
}

.pages_navigator{
      margin-top: 3rem;
      padding-top: 2.5rem;
      border-top: solid .1rem var(--border);
      height: auto;
      align-items: center;
      justify-content: space-around;
}

.btn_navigator{
      width: 4.4rem;
      height: 4.4rem;
      background: transparent;
      color: var(--text-100);
}

.btn_navigator *{
      font-size: 3rem;
}

.btn_navigator.active{
      color: var(--primary);
}

.pages_content{
      position: relative;
      margin-top: 3rem;
}

.page_content{
      width: 100%;
      height: auto;
      position: absolute;
      top: 0;
      left: 0;
      background: var(--bg-400);
      opacity: 0;
      visibility: hidden;
      transform: translateX(20%);
      transition: .5s ease-in-out;
      display: none;
      padding-bottom: 4rem;
}

.page_content.active{
      opacity: 1;
      transform: translateX(0);
      visibility: visible;
      display: block;
}

.video_box,
.video_box__item{
      width: 48.2rem;
      height: 27.2rem;
}

.video_box{
      margin-bottom: 3rem;
}

.video_box__item{
      border-radius: 1.5rem;
      overflow: hidden;
}

.video_box__item iframe{
      border: none;
      outline: none;
}