.btn_social__link{
      width: 4.2rem;
      height: 4.2rem;
      border-radius: 50%;
      color: var(--text-100);
}

.btn_social__link *{
      font-size: 2rem;
}

.btn_social__link.facebook{
      background: var(--facebook);
}

.btn_social__link.twitter{
      background: var(--twitter);
}

.btn_social__link.instagram{
      background: var(--instagram);
}

.btn_social__link.tiktok{
      background: var(--tiktok);
}