.relation_content{
      gap: 1rem;
}

.relation_avatar{
      width: 15rem;
      height: 15rem;
      border-radius: 50%;
      overflow: hidden;
      background: var(--v-gradient);
      padding: .9rem;
}

.relation_avatar__img{
      width: 13.2rem;
      height: 13.2rem;
      border-radius: 50%;
      overflow: hidden;
      box-shadow: 0 0 0 .5rem var(--bg-400);
}
.relation_data__title{
      flex-direction: column;
      align-items: flex-end;
      width: fit-content;
      margin-top: 3rem;
}

.relation_data__title h2{
      font-size: 3.5rem;
      font-weight: 500;
      font-family: var(--ft-6);
}

.relation_data__title strong{
      font-size: 2.5rem;
      color: var(--primary);
}

.relation_data > span{
      font-style: italic;
      color: var(--text-200);
}

.relation_navigator{
      gap: 1rem;
      align-items: center;
      margin-top: 1rem;
}

.relation_navigator a{
      width: 8rem;
      height: 2.8rem;
      border-radius: .4rem;
      border: solid .1rem var(--border);
      color: var(--text-100);
      line-height: 1.8rem;
      transition: .2s ease-in-out;
}

.relation_navigator a:hover{
      background: var(--light);
      border: solid .1rem transparent;
      color: var(--text-300);
}

.relation_description{
      margin-top: 3rem;
}

.relation_description p{
      white-space: pre-line;
      font-style: italic;
}